body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background: white;
}

p {
  font-size: calc(10px + 0.3em);
  margin-left: 80px;
  margin-right: 80px;
}

.page-body {
  margin-top: 185px;
  z-index: 0;
  text-align: center;
}

.important-text {
  color: #d8ad6c;
  /*font-weight: bold;*/
  /*font-family: 'Indie Flower', cursive;*/
  /*font-family: 'Spectral SC', serif;*/
  font-family: 'Vollkorn SC', serif;
  margin-top: 10px;
  margin-bottom: 0px;
}

.image-60 {
  width: 60%;
}

.image-50 {
  width: 50%;
}

.left-aligned {
  text-align: left;
  margin-top: -10px;
  text-indent: calc(20px + 1em);
}

.navbar {
  /*background-color: #D3D3D3;
  opacity: 0.8;*/
  height: 45px;
  text-align: center;
}

.navlinks {
  list-style-type: none;
  margin: 0px;
  padding: 0px;
}

.navlinks li {
  display: inline-block;
  vertical-align: top;
  line-height: 45px;
}

.navlink {
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: 0.5s ease;
  margin: 0vw 2vw;
  color: #FFB7C5;
  font-size: calc(12px + 0.45vw);
  /*font-family: Snell Roundhand, cursive;*/
  text-transform: uppercase;
}

.navlink:hover, .activated {
  border-bottom: 1px solid #FFB7C5;
}

.header {
  text-align: center;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 9;
  background-color: white;
}

.header-bg {
  height: 90px;
  background-size: auto 100%;
  background-repeat: no-repeat;
  background-position: top center;
  background-image: url(/kupo/static/media/champagne.6c14124e.png);
}

.header-text {
  font-family: Snell Roundhand, cursive;
  color: #d8ad6c;
  margin: 10px 0px 0px 0px;
}

.NoMatch-body {
  text-align: center;
}

.pending-page {
  background-color: white;
  text-align: center;
}

.pending-text {
  color: #FFB7C5;
  margin: 10px 0px 0px 0px;
}

.moogle-worker {
  width: 40%;
}

.event-container {
  margin: 50px 0px;
}

.rsvp-form {
  text-align: center;
  padding: 10px;
}

.button-pink {
  background-color: #FFB7C5;
  padding: 10px 20px;
  color: white;
  font-size: 14px;
  margin-top: 10px;
  margin-bottom: 20px;
}

.button-pink:hover {
  background-color: #ed768d;
}

.divider {
  width: 10%;
  margin-top: -10px;
  margin-bottom: -10px;
  -webkit-transform: rotate(60deg);
          transform: rotate(60deg);
}

.centered-table {
  margin-top: 15px;
  margin-bottom: 15px;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
}

.centered-table td {
  padding: 0px 15px;
}

.g-map {
  margin-top: 15px;
}

